.Header{
    margin-top: 35px;
    display: flex;
    flex-direction: row;
    height: 20%;
    width: 93.3%;
    max-width:none;
    flex:20px;
    position: absolute;
    margin-left: 5.2%;
  background-color: rgb(65, 64, 64);
  object-fit: contain;

  }
  .selector {
    margin-top: 15px;
    width: 100%;
    padding: 0 3px;
    color: #8a8989;
    font-size: small;
  }
  
  .selector-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px solid #8a8989;
    border-radius: 4px;
    padding: 8px;
    position: relative;
    width: 285px;
  }
  
  .selector-box span {
    position: absolute;
    bottom: 37px;
    background-color: #3c3a3a;
    padding: 0 2px;
  }
  .lower-sec {
    /* border-top: 1px solid #8a8989; */
    display: flex;
    float: right;
    /* justify-content:right; */
    align-items: center;
    font-size: small;
    padding: 7px;
    margin-top: 90px;
    position: absolute;
    object-fit: contain;
    margin-left: 89%;
    
  }
  .HR{
      width: 100%;
      margin-top: 90px;
      position: absolute;
      color: white;
      border-color: #636262;
  }
  
  .apply-button {
    display: flex;
    align-items: center;
    background-color: #636262;
    border-radius: 4px;
    padding: 8px 12px;
    margin-right: 3px;
    color: #939393;
  }
  
  .apply-button svg {
    font-size: 13px;
  }
  
  .cancel {
    padding-right: 10px;
    font-size: 11px;
    font-weight: 500;
  }
  
  .apply-text {
    padding-left: 5px;
  }
  