
.container{
  display: flex;
  flex-direction: row;
  height: 94vh;
  width: 98.5%;
  padding: 20px;
  padding-left: 0;
  background-color: rgb(44, 42, 42);
  object-fit: contain;
}

#input{
  display: flex;
  flex-direction: row;
  float:left;
  margin: 15px;
  padding: 10px;
  height: 20px;
  width: 260px;
  margin-bottom: 0;
  box-shadow: 5px 5px 5px rgb(36, 36, 36);
}
#Input-Text{
  font-size: 10px;
  color:rgb(143, 134, 134);
  height: 20px;
  width: 250px;
  margin-left: 28px; 
}

.chat-screen{
  margin-top: 14%;
  display: grid;
  grid-template-rows: 15% 15% auto;
  height: 70%;
  flex:65%;
  background-color: rgb(65, 64, 64);
  object-fit: contain;
  overflow-y:auto;
}

.btn{
    
  border: 2px solid black;
  border-radius: 6px;
  height: 30px;
}
input{
  background-color: transparent;
}
.input-box {
  display: flex;
  align-items: center;
  width: 95%;
  margin-left: 15px;
  margin-top: 400px;
  /* margin: 2 2 em; */
  border-radius: 4px;
  box-shadow: 0px 2px 4px 1px rgb(46 46 46);
  background-color: #3c3a3a;
  padding: 0.2em 0.5em;
  height: 25px;
}

.input-box svg {
  color: rgb(158, 158, 158);
  
}

.input-container {
  width: 100%;
}

.input-text {
  padding: 8px 1px;
  background-color: #3c3a3a;
  color: #d4d0d0;
  outline: none;
  border: none;
  width: 98%;
  border-bottom: 2px solid #585656;
  margin-left: 15px;
  font-size: 0.9em;
  font-weight: 500;
}

.input-text::-webkit-input-placeholder {
  font-size: 0.8em;
  font-weight: 500;
}
