.LogoContainer{
    display: flex;
    margin-top: 35px;
    flex-direction: column;
    height: 95%;
    flex:30px;
    margin-right: 20px;
    background-color: rgb(65, 64, 64);
    object-fit: contain;
  }
  .icon {
    padding: 8px 0;
    cursor: pointer;
    margin-left: 5px;
  }
  
  .icon svg {
    color: #fcf8f8;
    font-size: 1em;
  }