.ContactConatiner{
    margin-top: 14%;
    display: flex;
    flex-direction: column;
    height: 70%;
    flex:25%;
  margin-right: 20px;
  background-color: rgb(65, 64, 64);
  object-fit: contain;
  }
  .chat {
    display: grid;
  grid-template-columns: 30% 70%;
  margin: 0.7em;
    padding: 0.3em;
  height: 60px;
  color: rgb(143, 134, 134);
  background-color: rgb(65, 64, 64);
  box-shadow: 5px 5px 5px rgb(43, 39, 39);
  border-radius: 4px;
  }
  .profile-pic{
    max-width: 100%;  
    height: auto;  
  }

  
  .chat-data {
    padding-left: 0.7em;
  }
  
  .data {
    padding: 0.1em;
    color: #adacac;
  }
  
  .chat-List::-webkit-scrollbar {
    display: none;
  }